.button {
    padding: .7em 1.25em;
    background: linear-gradient(to left, #3229BD, #00E9F9);
    color: white;
    border-radius: 1.45em;
    margin: 1em;
    font-size: 1.2em;
    cursor: pointer;
    outline: none;
    border: none;
    transition: all .3s;
}
.button:focus,
.button:hover {
    box-shadow: 0px 5px 15px 0px rgba(0,0,0,0.5);
}